import { ADDHEALTHDETAILS, ADDHEALTHNUMBER, ALLHEALTHDETAILS, HEALTHNUMFLAG } from "../actiontypes/NumHealthActiontypes"

export const numhealthflagfalse=()=>{
    return{
        type:HEALTHNUMFLAG
    }
}
export const allhealthdetails=()=>{
    return{
        type:ALLHEALTHDETAILS
    }
}
export const addhealthnum=(data)=>{
    console.log(data)
    return{
        type:ADDHEALTHNUMBER,
        payload:data
    }
}
export const addhealthdetails=(data)=>{
    return{
        type:ADDHEALTHDETAILS,
        payload:data
    }
}
