import { ADDCARDETAILS, ADDCARNUMBER, ALLCARDETAILS, CARNUMFLAG } from "../actiontypes/NumCarActiontypes";

const init={
    iscarno:false,
    carnum:"",
    carmake:"",
    carmodel:"",
    caryear:"",
    isallcardetails:false
  };
export const numCarReducer=(state=init,{type,payload})=>{
    switch (type) {
        case ADDCARNUMBER:
            return {...state,carnum:payload,iscarno:true};
        case CARNUMFLAG:
            return {...state,carnum:"",iscarno:false}
        case ADDCARDETAILS:
            return {...state,carmake:payload.make,carmodel:payload.model,caryear:payload.year,isallcardetails:true}
        case ALLCARDETAILS:
            return {...state,isallcardetails:true}
        default:
            return {...state}
    }
}