import { ADDCOMMERCIALDETAILS, ADDCOMMERCIALNUMBER, ALLCOMMERCIALDETAILS, COMMERCIALNUMFLAG } from "../actiontypes/NumCommercialActiontypes";

const init={
    iscommercialno:false,
    commercialnum:"",
    commercialmake:"",
    commercialmodel:"",
    commercialyear:"",
    isallcommercialdetails:false
  };
export const numCommercialReducer=(state=init,{type,payload})=>{
    switch (type) {
        case ADDCOMMERCIALNUMBER:
            return {...state,commercialnum:payload,iscommercialno:true};
        case COMMERCIALNUMFLAG:
            return {...state,commercialnum:"",iscommercialno:false}
        case ADDCOMMERCIALDETAILS:
            return {...state,commercialmake:payload.make,commercialmodel:payload.model,commercialyear:payload.year,isallcommercialdetails:true}
        case ALLCOMMERCIALDETAILS:
            return {...state,isallcommercialdetails:true}
        default:
            return {...state}
    }
}