import thunk from "redux-thunk";
import {numBikeReducer} from "../Reducer/NumBikereducer"
import {numCarReducer} from "../Reducer/NumCarreducer"
import {numCommercialReducer} from "../Reducer/NumCommercialreducer"
import {numLifeReducer} from "../Reducer/NumLifereducer"
import {numHealthReducer} from "../Reducer/NumHealthreducer"
import {numTravelReducer} from "../Reducer/NumTravelreducer"
import {applyMiddleware, combineReducers, compose, createStore} from "redux"
import {reducer} from "../reducer"
const rootReducer = combineReducers({
    vehicledetail:numBikeReducer,
    cardetail:numCarReducer,
    healthdetail:numHealthReducer,
    traveldetail:numTravelReducer,
    commercialdetail:numCommercialReducer,
    lifedetail:numLifeReducer,
    addressdetail:reducer,
})

export const store=createStore(
    rootReducer,
    compose(applyMiddleware(thunk), 
            //window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
))