import { ADDHEALTHDETAILS, ADDHEALTHNUMBER, ALLHEALTHDETAILS, HEALTHNUMFLAG } from "../actiontypes/NumHealthActiontypes";

const init={
    ishealthno:false,
    healthnum:"",
    healthmake:"",
    healthmodel:"",
    healthyear:"",
    isallhealthdetails:false
  };
export const numHealthReducer=(state=init,{type,payload})=>{
    switch (type) {
        case ADDHEALTHNUMBER:
            return {...state,healthnum:payload,ishealthno:true};
        case HEALTHNUMFLAG:
            return {...state,healthnum:"",ishealthno:false}
        case ADDHEALTHDETAILS:
            return {...state,healthmake:payload.make,healthmodel:payload.model,healthyear:payload.year,isallhealthdetails:true}
        case ALLHEALTHDETAILS:
            return {...state,isallhealthdetails:true}
        default:
            return {...state}
    }
}