import { ADDLIFEDETAILS, ADDLIFENUMBER, ALLLIFEDETAILS, LIFENUMFLAG } from "../actiontypes/NumLifeActiontypes"

export const numlifeflagfalse=()=>{
    return{
        type:LIFENUMFLAG
    }
}
export const alllifedetails=()=>{
    return{
        type:ALLLIFEDETAILS
    }
}
export const addlifenum=(data)=>{
    console.log(data)
    return{
        type:ADDLIFENUMBER,
        payload:data
    }
}
export const addlifedetails=(data)=>{
    return{
        type:ADDLIFEDETAILS,
        payload:data
    }
}
