import { ADDTRAVELDETAILS, ADDTRAVELNUMBER, ALLTRAVELDETAILS, TRAVELNUMFLAG } from "../actiontypes/NumTravelActiontypes ";

const init={
    istravelno:false,
    travelnum:"",
    travelmake:"",
    travelmodel:"",
    travelyear:"",
    isalltraveldetails:false
  };
export const numTravelReducer=(state=init,{type,payload})=>{
    switch (type) {
        case ADDTRAVELNUMBER:
            return {...state,travelnum:payload,istravelno:true};
        case TRAVELNUMFLAG:
            return {...state,travelnum:"",istravelno:false}
        case ADDTRAVELDETAILS:
            return {...state,travelmake:payload.make,travelmodel:payload.model,travelyear:payload.year,isalltraveldetails:true}
        case ALLTRAVELDETAILS:
            return {...state,isalltraveldetails:true}
        default:
            return {...state}
    }
}