import { ADDLIFEDETAILS, ADDLIFENUMBER, ALLLIFEDETAILS, LIFENUMFLAG } from "../actiontypes/NumLifeActiontypes";

const init={
    islifeno:false,
    lifenum:"",
    lifemake:"",
    lifemodel:"",
    lifeyear:"",
    isalllifedetails:false
  };
export const numLifeReducer=(state=init,{type,payload})=>{
    switch (type) {
        case ADDLIFENUMBER:
            return {...state,lifenum:payload,islifeno:true};
        case LIFENUMFLAG:
            return {...state,lifenum:"",islifeno:false}
        case ADDLIFEDETAILS:
            return {...state,lifemake:payload.make,lifemodel:payload.model,lifeyear:payload.year,isalllifedetails:true}
        case ALLLIFEDETAILS:
            return {...state,isalllifedetails:true}
        default:
            return {...state}
    }
}