import { ADDCARDETAILS, ADDCARNUMBER, ALLCARDETAILS, CARNUMFLAG } from "../actiontypes/NumCarActiontypes"

export const numCarflagfalse=()=>{
    return{
        type:CARNUMFLAG
    }
}
export const allcardetails=()=>{
    return{
        type:ALLCARDETAILS
    }
}
export const addcarnum=(data)=>{
    console.log(data)
    return{
        type:ADDCARNUMBER,
        payload:data
    }
}
export const addcardetails=(data)=>{
    return{
        type:ADDCARDETAILS,
        payload:data
    }
}
