import { ADDCOMMERCIALDETAILS, ADDCOMMERCIALNUMBER, ALLCOMMERCIALDETAILS, COMMERCIALNUMFLAG } from "../actiontypes/NumCommercialActiontypes"

export const numCommercialflagfalse=()=>{
    return{
        type:COMMERCIALNUMFLAG
    }
}
export const allcommercialdetails=()=>{
    return{
        type:ALLCOMMERCIALDETAILS
    }
}
export const addcommercialnum=(data)=>{
    return{
        type:ADDCOMMERCIALNUMBER,
        payload:data
    }
}
export const addcommercialdetails=(data)=>{
    return{
        type:ADDCOMMERCIALDETAILS,
        payload:data
    }
}
